// 思路： 孩子显不显示 靠 维持数据来 ，isExpand 
// 两种侧边栏 靠expanded 来显示
// 显示在滚动区域 margin-left auto 可以flex 布局漂移

import React, {
    FunctionComponentElement, CSSProperties, ReactElement, useCallback, useEffect, useRef, useState,
} from 'react';
import classnames from 'classnames';
import styles from './index.less';
import FlexContainer from '../FlexContainer';
import Text from '../Text';
import FlexItem from '../FlexItem';
import UpCare from 'component/Icon/UpCare';


/** 菜单选项配置 */
export interface SideMenuOption{
    /** 菜单展示名称 */
    name:string,
    /** 菜单key，onSelect参数 */
    key:string,
    /** 子菜单项 */
    sub?:SideMenuOption[],
    /** 菜单项图标 */
    icon?:ReactElement,
    /** 菜单项后缀 */
    suffix?:ReactElement,
}

export interface SideMenuProps{
    /** 菜单项配置 */
    menus:SideMenuOption[],
    /** 菜单项选中回调 */
    onSelect:(itemKey:string)=>void,
    /**
     * 是否使用折叠菜单
     * @默认值 false
     */
    expanded?:boolean,
    /**
     * 菜单列表前置元素
     * @默认值 undefined
     * */
    prefix?:ReactElement,
    /**
     * 菜单列表后置元素
     * @默认值 undefined
     * */
    suffix?:ReactElement,
    /**
     * 默认展开的菜单的key
     * @默认值 []
     * */
    initialExpandedKeys?:string[],
    /**
     * 选中的菜单的key
     * @默认值 undefined
     */
    selectedKey:string,
    /**
     * 菜单容器样式
     * @默认值 undefined
     */
    className?:string,
    /**
     * 菜单容器样式
     * @默认值 undefined
     */
    style?:CSSProperties
}


const SideMenu = (props:SideMenuProps):FunctionComponentElement<SideMenuProps> => {
    const {
        menus, expanded = false, prefix, suffix, initialExpandedKeys = [], selectedKey, onSelect,
        className, style,
    } = props;

    const [expandedKeys, setExpandedKeys] = useState(initialExpandedKeys);

    const scrollRef = useRef<HTMLDivElement>(null);
   // 有bug 如果路由是动态的
    useEffect(() => {
        if (scrollRef.current && scrollRef.current.scrollHeight > scrollRef.current.offsetHeight) {
            const selectedItem = scrollRef.current.querySelector(`.${styles.active}`)as HTMLDivElement;
            if (selectedItem) {
                const { offsetTop } = selectedItem;
                const { offsetHeight } = scrollRef.current;
                if (offsetTop > offsetHeight) {
                    scrollRef.current.scrollTop = offsetTop - offsetHeight;
                }
            }
        }
    }, []);

    const toggleExpand = useCallback((key:string) => {
        setExpandedKeys((oldKeys:any) => {
            if (oldKeys === undefined) {
                return [key];
            }
            if (oldKeys.includes(key)) {
                return oldKeys.filter((k:any) => k !== key);
            }
            return oldKeys.concat([key]);
        });
    }, [setExpandedKeys]);

    const renderMenuTitle = (item:SideMenuOption, isExpand:boolean):ReactElement => {
        if (expanded) {
            return (
                <div
                    className={classnames(styles.expandableMenuTitle, {
                        [styles.expand]: isExpand,
                    })}
                    onClick={() => {
                        toggleExpand(item.key);
                    }}
                    key={item.key}
                >
                    {item.icon ? item.icon : undefined}
                    <Text>{item.name}</Text>
                    {item.suffix ? item.suffix : undefined}
                    <span className={styles.arrow}>< UpCare /></span>
                </div>
            );
        }
        return (
            <div className={styles.normalMenuTitle} key={item.key}>
                {item.icon ? item.icon : undefined}
                <Text level="tips" className={styles.normalMenuName}>{item.name}</Text>
                {item.suffix ? item.suffix : undefined}
            </div>
        );
    };

    const renderMenu = (item:SideMenuOption):ReactElement => (
        <div
            key={item.key}
            className={classnames(styles.menuItem, {
                [styles.active]: selectedKey === item.key,
                [styles.expandedMenuItem]: expanded,
            })}
            onClick={() => {
                if (item.key !== selectedKey) {
                    onSelect(item.key);
                }
            }}
        >
            {item.icon ? item.icon : undefined}
            <Text>{item.name}</Text>
            {item.suffix ? item.suffix : undefined}
        </div>
    );


    return (
        <FlexContainer flex="vertical-full" className={className} style={style}>
            {prefix ? <FlexItem>{prefix}</FlexItem> : undefined}
            <FlexItem grow scroll className={styles.menus} ref={scrollRef}>
                {
                    menus.reduce((result:(ReactElement)[], item:any):(ReactElement)[] => {
                        const isExpand = expanded === false || expandedKeys.includes(item.key);
                        result.push(renderMenuTitle(item, isExpand));
                        if (item.sub && item.sub.length > 0 && isExpand) {
                            return result.concat(item.sub.map(renderMenu));
                        }
                        return result;
                    }, [])
                }
            </FlexItem>
            {suffix ? <FlexItem>{suffix}</FlexItem> : undefined}
        </FlexContainer>
    );
};

export default SideMenu;
