export default "import styles from './index.less'\nimport SideMenu from 'component/SideMenu';\nimport FlexContainer from 'component/FlexContainer';\nimport FlexItem from 'component/FlexItem';\nimport { useState } from 'react';\nimport Button from 'component/Button';\nimport Person from 'component/Icon/Person';\nimport Sale from 'component/Icon/Sale';\n\nconst menus = [\n    {\n        name: '我的任务',\n        key: 'task',\n        sub: [\n            {\n                name: '今日截止',\n                key: 'stop',\n                suffix: <span className={styles.number}>9</span>\n            },\n            {\n                name: '待执行',\n                key: 'start',\n                suffix: <span className={styles.number}>3</span>\n            },\n            {\n                name: '已结束',\n                key: 'end'\n            }\n        ]\n    },\n    {\n        name: '团队任务',\n        key: 'group',\n        sub: [{\n            icon: <Person className={styles.icon} />,\n            name: '团队的待执行',\n            key: 'groupstart'\n        },\n        {\n            icon: <Sale className={styles.icon}/>,\n            name: '团队的已结束',\n            key: 'groupend'\n        }]\n    }\n]\n\n\nconst NormalSideDemo = () => {\n\n    const [selected, setSelected] = useState('stop');\n    const handleSelected = (key: string) => {\n        setSelected(key)\n    }\n\n    const getName = (result: string, menu: any) => {\n        if (result !== '') return result;\n        if (menu.sub) {\n            return menu.sub.reduce(getName, result)\n        }\n        if (menu.key === selected) return menu.name;\n        return ''\n    }\n    return (\n        <div className={styles.container}>\n            <FlexContainer flex=\"horizontal-full\">\n                <FlexItem basis={200} className={styles.item}>\n                    <SideMenu\n                        menus={menus}\n                        selectedKey={selected}\n                        onSelect={handleSelected}\n                        prefix={\n                            <div\n                                className={styles.prefix}>\n                                <Button\n                                    style={{ width: '100%' }}\n                                    type=\"contained\" primary\n                                    size=\"l\"\n                                >\n                                    新建任务\n                                </Button></div>\n                        }\n                    />\n                </FlexItem>\n                <FlexItem>\n                    <div className={styles.content}>\n                        当前选择中的{menus.reduce(getName, '')}\n                    </div>\n                </FlexItem>\n            </FlexContainer >\n        </div>\n    )\n}\n\nexport default NormalSideDemo;\n";